<template>
  <div >
    <div class="btn-group" role="group">
      <button type="button" class="btn btn-default" @click="changeBody('/home/')">主页</button>
      <button type="button" class="btn btn-info" @click="changeBody('/project/')">项目</button>
      <button type="button" class="btn btn-success" @click="changeBody('/blog/')">博客</button>
      <button type="button" class="btn btn-danger" @click="changeBody('/contact/')">联系我</button>
    </div>
  </div>

</template>

<script>
  export default{
    name: 'barMenu',
    data () {
      return{
      }
    },
    methods:{
      changeBody(val){
        this.$emit('message',val);
      }
    }
  }
</script>

<style>

</style>



