import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/views/Header'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Header',
    component: Header,
    meta: {requiresAuth: true},
    children: [{
      path: '/contact/:randKey',
      name: 'Contact',
      component: () => import('@/components/articles/contact')
    }, {
      path: '/home/:randKey',
      name: 'Home',
      component: () => import('@/components/articles/home')
    }, {
      path: '/blog/:randKey',
      name: 'Blog',
      component: () => import('@/components/articles/blog')
    }, {
      path: '/project/:randKey',
      name: 'Project',
      component: () => import('@/components/articles/project')
    }]
  }]
})



